*{
    --primary-color: #DDA0DD;
    --secondary-color: #ffffff;
}

.listing-page-container{
    display: flex;
    flex-wrap: wrap;
}
.basic-card-container{
    background-color: var(--badge-color);
    padding: 1rem;
    border-radius: 0.3rem;
    margin: 1rem;
    max-width: 30rem;
    box-shadow: 1px 1px 15px 0 rgb(172, 170, 170);
}
.card-heading{
    font-size: 2rem;
    margin: 0.5rem;
    color: var(--secondary-color);
}
.card-subheading{
    font-size: 1rem;
    margin: 0 0 0.5rem 0.5rem;
}
.card-body{
    margin: 0 0.5rem 0.5rem 0.5rem;
}
.card-footer{
    margin: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.btn{
    font-size: 1rem;
    border: none;
    background-color: var(--primary-color);
    color: var(--badge-color);
    cursor: pointer;
}
.btn.wo-outline{
    color: var(--primary-color);
    background-color: var(--badge-color);
}
.card-photo{
    width:100%;
    max-width: 30rem;
    max-height: 17rem;
    border-radius: 0.3rem;
}
.photo-card-container .basic-card-container{
    position: relative;
    padding : 0;
}
.text-on-picture .card-heading{
    position: absolute;
    top : 53%;
}
.photo-card-container .card-footer{
    padding-bottom: 1rem;
}
.text-on-picture .card-subheading{
    position: absolute;
    top : 68%;
}
.nav-container{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: var(--primary-color);
    color: var(--secondary-color);
}

.logo{
    font-size: 2.5rem;
    margin: 1rem;
    color: var(--secondary-color);
    margin-left: 2rem;
}
.logo-link{
    text-decoration: none;
}
.nav-input{
    min-width: 6rem;
    font-size: 2rem;
}
.nav-input{
    width: 800px;
    resize: vertical;
}
.menu-items{
    display: flex;
}
.menu-items .nav-ul{
    margin: 0;
    margin-right: 1rem;
    padding: 0;
    display: flex;
}
.menu-items .list-item{
    list-style: none;
}
.menu-items .list-item-left{
    font-size: 2rem;
    display: flex;
    align-items: center;
    /* cursor: pointer; */
}
.menu-items{
    display: flex;
    align-items: center;
    color: var(--secondary-color);
    cursor: pointer;
}

.menu-items .list-item .menu-link {
    text-decoration: none;
    color: var(--secondary-color);
    font-size: 2rem;
    margin: 1rem;
    display: block;
}

.nav-menu{
    background-color: rgba(221, 160, 221, 0.5);
    width: 20rem;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
}

.nav-text{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0.5rem 0 0.5rem 1rem;
    list-style: none;
    height: 2rem;
    font-size: 1.5rem;
    
}
.nav-text-link{
    text-decoration: none;
    color: var(--secondary-color);
}
.content-container{
    width: 100%;
    /* height: 80vh; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.login-container{
    background-color: white;
    display: flex;
    max-width: 80rem;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    /* margin-top: 5rem; */
    /* padding: 7rem 10rem; */
    box-shadow: 0 0.01rem 0.01rem 0.01rem black;
}
.login-elements{
    display: flex;
    max-width: 90rem;
    width: 50rem;
    height: 50rem;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    /* margin-top: 10rem; */
    padding: 7rem 10rem;
}
.login-hero{
    font-size: 4rem;
    padding: 1rem;
    padding-bottom: 2rem;
    font-weight: bolder;
}
.login.email,.login.password  {
    display: flex;
}
.login.email label{
    align-self: flex-start;
}
.login.password label{
    align-self: flex-start;
}
.login input[type= "text"]{
    width: 400px;
    height: 48px;
    font-size: 1rem;
}
.password-block{
    width: 25rem;
    height: 3rem;
    font-size: 2rem;

}
.login{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    padding-left: 0;
    padding-bottom: 1rem;
    font-size: 1.5rem;
    font-weight: lighter;
}
.login.details{
    flex-direction: row;
    width: -webkit-max-content;
    width: max-content;
    vertical-align: middle;
}
.remember-check label,input{
    display: inline-block;
    vertical-align: middle;

}
.remember-check input{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
.forgot-password{
    text-decoration: none;
    padding: 2rem;
}
.button{
    padding: 0.5rem 1rem;
    background-color: var(--primary-color);
    border-radius: 0.5rem;
    cursor: pointer;
    opacity: 100%;
    width: 12rem;
    height: 2.5rem;
    color: var(--secondary-color);
    border: none;
    font-size: 1rem;
    display: block;
    margin: 2rem;
    text-align: center;
    text-decoration: none;
}
.button.secondary{
    background-color: var(--secondary-color);
    color: var(--primary-color)
}
.links{
    text-decoration: none;
    color: var(--secondary-color);
}
.content-area{
    display: flex;
    margin : 0rem;
}
.lottie-hero-container{

}
.hero-heading-lottie{
    padding : 2rem;
    font-size: 2rem;
}
.hero-content-area{
    max-height: auto;
    text-align: center;
}
.hero-buttons{
    display: flex;
    padding: 2rem;
    justify-content: center;
}
.hero-button{
    padding: 0.5rem 1rem;
    background-color: var(--primary-color);
    border-radius: 0.5rem;
    cursor: pointer;
    opacity: 100%;
    width: 8rem;
    height: 2.5rem;
    color: var(--secondary-color);
    border: none;
    font-size: 1rem;
}
.watch-button{
    padding: 1.5rem;
}

